.mat-form-field+.mat-form-field {
  margin-left: 8px;
}

.mat-expansion-panel {
  margin-bottom: 10px;
  box-shadow: 0px 0px 7px -1px rgba(0, 0, 0, 0.08), 0 1px 1px 0 rgba(0, 0, 0, 0),
    0 1px 3px 0 rgba(0, 0, 0, 0) !important;
  border-radius: 10px !important;
}

label {
  display: inline-block;
  margin-bottom: 0;
  color: #3e3e3e;
  font-size: 13px !important;
  font-family: "GothamNarrowBold", sans-serif !important;
  max-width: 191px;
  word-break: break-word;
}

.lang_label {
  font-family: "gothambook", sans-serif !important;
}

#status-toggle .btn-toggle:before {
  /* content: "OFF" !important; */
  content: attr(data-off);
  left: -6.5rem !important;
}

#status-toggle .btn-toggle:after {
  /* content: "ON" !important; */
  content: attr(data-on);
  right: -4.9rem !important;
}

#status-toggle .btn-toggle {
  /* margin: 0 1.5rem !important; */
}

textarea {
  resize: none !important;
}

.mat-form-field {
  width: 65% !important;
}

.alert-dark {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 115px;
  margin-top: 16px;
  color: #818181;
  background-color: #f2f2f5;
  border-color: #c6c8ca;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 17px;
  border-radius: 8px;
}

.alert-dark img {
  cursor: pointer;
}

.alert-dismissible {
  padding-right: 10px;
}

#profile .mat-expansion-panel-body .view-mode .mat-form-field-appearance-outline .mat-form-field-outline-end,
#profile .mat-expansion-panel-body .view-mode .mat-form-field-appearance-outline .mat-form-field-outline-start {
  border: none !important;
  min-width: 5px;
  background: #fff !important;
}

.email-block {
  z-index: 999999;
}

.label_text {
  word-break: break-all;
  font-family: "gothambook", sans-serif;
  padding: 0 10.5px;
}

.view-mode .drop_view {
  margin-top: -11px;
}

.profile_form .col {
  display: flex;
  margin-bottom: 14px;
  align-items: baseline;
}

.bus_detail {
  color: rgba(0, 0, 0, 0.87) !important;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 16px;
}

.span_font {
  color: #3e3e3e;
  font-size: 13px !important;
  font-family: "gothambook", sans-serif !important;
}

.noti_label {
  padding-right: 10px;
}

.preferences .noti_label {
  width: 150px !important;
}

.width-set {
  width: 421px !important;
}

.mat-expansion-panel-header-description,
.mat-expansion-panel-header-title {
  font-size: 16px;
}

.s-input .mat-form-field {
  margin-right: 0px !important;
}

.edit-pref-display {
  display: flex;
}

.view-mode .edit-pref-display {
  display: flex;
}

@media screen and (max-width: 575px) {
  .view-mode .edit-pref-display {
    display: flex;
  }

  .lang_label {
    max-width: 100% !important;
    width: 100%;
  }

  .edit-pref-display {
    display: block;
  }

  .span_font {
    width: 100%;
    display: block;
  }

  .preferences .noti_label {
    width: 100% !important;
  }

  .width-set {
    width: 100% !important;
  }
}

::ng-deep .profileForm1 .mat-select-arrow-wrapper {
  top: 4px !important;
}

@media screen and (max-width: 695px) and (min-width: 576px) {
  .notify-radio-d {
    display: block !important;
  }

  .radio-margin {
    margin-top: 5px;
  }

  .notify-label-align {
    align-items: flex-start !important;
  }
}

@media screen and (max-width: 623px) and (min-width: 576px) {
  .width-set {
    width: 374px !important;
  }
}

@media screen and (max-width: 510px) {
  .notify-radio-d {
    display: block !important;
  }

  .radio-margin {
    margin-top: 5px;
  }

  .notify-label-align {
    align-items: flex-start !important;
  }
}

@media screen and (max-width: 510px) {
  .hour-time-radio-d {
    display: block !important;
  }

  .hour-time-margin {
    margin-top: 5px;
  }

  .hour-time-label-align {
    align-items: flex-start !important;
  }
}